import {
  FETCH_GRAPH_DATA,
  CREATE_NODE,
  UPDATE_NODE,
  DELETE_NODE,
  ADD_LINK,
  DELETE_LINK,
} from './constants';
import { formatNodes } from '../../helpers';

const initialState = {
  fetching: {
    selectedGraph: false,
    createNode: false,
    updateNode: false,
    deleteNode: false,
    addLink: false,
    deleteLink: false,
  },
  selectedGraph: null,
  orphans: null,
  displayedGraph: null,
  nodesByIds: null,
};

const workspaceReducer = (state = initialState, action = {}) => {
  switch (action.type) {
    case `${FETCH_GRAPH_DATA}_PENDING`:
      return {
        ...state,
        fetching: { ...state.fetching, selectedGraph: true },
      };
    case `${FETCH_GRAPH_DATA}_FULFILLED`: {
      const { orphans: rawOrphans, ...graphData } = action.payload;
      const { nodes: rawNodes } = graphData;
      const nodes = formatNodes(rawNodes);
      const orphans = formatNodes(rawOrphans);
      const selectedGraph = {
        ...graphData,
        nodes,
        focusedNodeId: '',
      };
      const nodesByIds = [...nodes, ...orphans].reduce(
        (acc, node) => ({ ...acc, [node.id]: { ...node } }),
        {}
      );
      return {
        ...state,
        selectedGraph,
        orphans,
        nodesByIds,
        fetching: { ...state.fetching, selectedGraph: false },
      };
    }
    case `${FETCH_GRAPH_DATA}_REJECTED`:
      return {
        ...state,
        fetching: { ...state.fetching, selectedGraph: false },
      };
    case `${CREATE_NODE}_PENDING`:
      return {
        ...state,
        fetching: {
          ...state.fetching,
          createNode: true,
        },
      };
    case `${CREATE_NODE}_FULFILLED`:
      return {
        ...state,
        fetching: {
          ...state.fetching,
          createNode: false,
        },
      };
    case `${CREATE_NODE}_REJECTED`:
      return {
        ...state,
        fetching: {
          ...state.fetching,
          createNode: false,
        },
      };
    case `${UPDATE_NODE}_PENDING`:
      return {
        ...state,
        fetching: { ...state.fetching, updateNode: true },
      };
    case `${UPDATE_NODE}_FULFILLED`:
      return {
        ...state,
        fetching: { ...state.fetching, updateNode: false },
      };
    case `${UPDATE_NODE}_REJECTED`:
      return {
        ...state,
        fetching: { ...state.fetching, updateNode: false },
      };
    case `${DELETE_NODE}_PENDING`:
      return {
        ...state,
        fetching: { ...state.fetching, deleteNode: true },
      };
    case `${DELETE_NODE}_FULFILLED`:
      return {
        ...state,
        fetching: { ...state.fetching, deleteNode: false },
      };
    case `${DELETE_NODE}_REJECTED`:
      return {
        ...state,
        fetching: { ...state.fetching, deleteNode: false },
      };
    case `${ADD_LINK}_PENDING`:
      return {
        ...state,
        fetching: { ...state.fetching, addLink: true },
      };
    case `${ADD_LINK}_FULFILLED`:
      return {
        ...state,
        fetching: { ...state.fetching, addLink: false },
      };
    case `${ADD_LINK}_REJECTED`:
      return {
        ...state,
        fetching: { ...state.fetching, addLink: false },
      };
    case `${DELETE_LINK}_PENDING`: {
      return {
        ...state,
        fetching: { ...state.fetching, deleteLink: true },
      };
    }
    case `${DELETE_LINK}_FULFILLED`: {
      return {
        ...state,
        fetching: { ...state.fetching, deleteLink: false },
      };
    }
    case `${DELETE_LINK}_REJECTED`: {
      return {
        ...state,
        fetching: { ...state.fetching, deleteLink: false },
      };
    }
    default:
      return state;
  }
};

export default workspaceReducer;
