import axios from 'axios';

export const getApiElements = (url, actionName) => {
  const headers = {};
  const request = axios.get(url, headers);

  return dispatch => {
    dispatch({
      type: `${actionName}_PENDING`,
    });
    return request
      .then(({ data }) => {
        dispatch({
          type: `${actionName}_FULFILLED`,
          payload: data,
        });
      })
      .catch(error => {
        dispatch({
          type: `${actionName}_REJECTED`,
          payload: error.message,
        });
      });
  };
};

export const postApiElements = (url, actionName, newObject) => {
  const request = axios.post(url, newObject);
  return dispatch => {
    dispatch({ type: `${actionName}_PENDING` });

    return request
      .then(({ data }) => {
        dispatch({
          type: `${actionName}_FULFILLED`,
          payload: data,
        });
      })
      .catch(error => {
        dispatch({
          type: `${actionName}_REJECTED`,
          payload: error.message,
        });
      });
  };
};

export const putApiElement = (url, actionName, updatedObject) => {
  const request = axios.put(url, updatedObject);

  return dispatch => {
    dispatch({ type: `${actionName}_PENDING` });

    return request
      .then(({ data }) => {
        dispatch({
          type: `${actionName}_PENDING`,
          payload: data,
        });
      })
      .catch(error => {
        dispatch({
          type: `${actionName}_REJECTED`,
          payload: error.message,
        });
      });
  };
};

export const deleteApiElement = (url, actionName, requestBodyPayload) => {
  const headers = {};
  const request = axios.delete(url, {
    ...headers,
    ...(requestBodyPayload && Object.keys(requestBodyPayload).length > 0
      ? { data: requestBodyPayload }
      : {}),
  });

  return dispatch => {
    dispatch({
      type: `${actionName}_PENDING`,
      payload: requestBodyPayload,
    });

    return request
      .then(({ data }) => {
        dispatch({
          type: `${actionName}_FULFILLED`,
          payload: data,
        });
      })
      .catch(error => {
        dispatch({
          type: `${actionName}_REJECTED`,
          payload: error.message,
        });
      });
  };
};
