import React, { useState } from 'react';
import { Redirect } from 'react-router-dom';

import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import InputAdornment from '@material-ui/core/InputAdornment';
import LockIcon from '@material-ui/icons/Lock';
import MailIcon from '@material-ui/icons/Mail';
import TextField from '@material-ui/core/TextField';
import Typography from '@material-ui/core/Typography';
import VerifiedUserIcon from '@material-ui/icons/VerifiedUser';

import { useAuth } from '../../hooks/useAuth';

const useStyles = makeStyles({
  wrapper: {
    flexGrow: 1,
    width: '60%',
    margin: '0 auto',
    display: 'flex',
    justifyContent: 'center',
    flexDirection: 'column',
    padding: '3rem',
  },
  header: {
    fontWeight: 700,
    letterSpacing: '1.5px',
  },
  errorWrapper: {
    minHeight: '1.5rem',
  },
  successMessage: {
    color: '#4caf50',
  },
  hidden: {
    visibility: 'hidden',
  },
  form: {
    display: 'flex',
    flexDirection: 'column',
    maxWidth: '25rem',
    marginTop: '1rem',
  },
  input: {
    marginBottom: '1.5rem',
  },
  buttonWrapper: {
    display: 'flex',
    justifyContent: 'flex-end',
  },
});

const handleChangeUserProperty = fn => event => {
  const {
    target: { value },
  } = event;
  fn(value);
};

const ForgotPasswordConfirmationPage = () => {
  const classes = useStyles();
  const { isAuthenticated, submitNewPassword } = useAuth();

  const [email, setEmail] = useState('');
  const [code, setCode] = useState('');
  const [newPassword, setNewPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [error, setError] = useState('');
  const [confirmMessage, setConfirmMessage] = useState('');

  const clearPasswordValues = () => {
    setEmail('');
    setCode('');
    setNewPassword('');
    setConfirmPassword('');
  };

  const handleSubmit = e => {
    e.preventDefault();
    if (error) setError('');
    if (confirmMessage) setConfirmMessage('');
    if (newPassword === confirmPassword) {
      submitNewPassword(email, code, newPassword)
        .then(() => {
          clearPasswordValues();
          setConfirmMessage('Your password has been changed successfully');
        })
        .catch(err => {
          setError(err.message);
        });
    } else {
      setError('Passwords do not match');
    }
  };

  if (isAuthenticated()) {
    return <Redirect to="workspace" />;
  }

  return (
    <div className={classes.wrapper}>
      <Typography className={classes.header} variant="h4" component="h1" color="textPrimary">
        Change password
      </Typography>
      <div className={classes.errorWrapper}>
        {error ? (
          <Typography variant="body1" component="p" color="error">
            {error}
          </Typography>
        ) : (
          <Typography className={classes.successMessage} variant="body1" component="p">
            {confirmMessage}
          </Typography>
        )}
      </div>
      <form className={classes.form} onSubmit={handleSubmit}>
        <TextField
          className={classes.input}
          label="E-MAIL"
          type="email"
          value={email}
          onChange={handleChangeUserProperty(setEmail)}
          InputProps={{
            startAdornment: (
              <InputAdornment position="start">
                <MailIcon />
              </InputAdornment>
            ),
          }}
          required
        />
        <TextField
          className={classes.input}
          label="VERIFICATION CODE"
          type="number"
          value={code}
          onChange={handleChangeUserProperty(setCode)}
          InputProps={{
            startAdornment: (
              <InputAdornment position="start">
                <VerifiedUserIcon />
              </InputAdornment>
            ),
          }}
          required
        />
        <TextField
          className={classes.input}
          label="NEW PASSWORD"
          type="password"
          value={newPassword}
          onChange={handleChangeUserProperty(setNewPassword)}
          InputProps={{
            startAdornment: (
              <InputAdornment position="start">
                <LockIcon />
              </InputAdornment>
            ),
          }}
          required
        />
        <TextField
          className={classes.input}
          label="CONFIRM NEW PASSWORD"
          type="password"
          value={confirmPassword}
          onChange={handleChangeUserProperty(setConfirmPassword)}
          InputProps={{
            startAdornment: (
              <InputAdornment position="start">
                <LockIcon />
              </InputAdornment>
            ),
          }}
          required
        />
        <div className={classes.buttonWrapper}>
          <Button type="submit" variant="contained" color="primary">
            Change password
          </Button>
        </div>
      </form>
    </div>
  );
};

export default ForgotPasswordConfirmationPage;
