import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';

import { makeStyles } from '@material-ui/core/styles';
import AccountCircle from '@material-ui/icons/AccountCircle';
import AppBar from '@material-ui/core/AppBar';
import BubbleChart from '@material-ui/icons/BubbleChart';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import Menu from '@material-ui/core/Menu';
import MenuIcon from '@material-ui/icons/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';

import { useAuth } from '../../hooks/useAuth';

const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
    color: '#fff',
  },
  button: {
    color: '#fff',
  },
  link: {
    color: 'inherit',
  },
  paragraph: {
    marginRight: '1.5rem',
  },
}));

const NavBar = () => {
  const [userName, setUserName] = useState('');
  const [anchorEl, setAnchorEl] = useState(null);
  const { logOut, user } = useAuth();
  const classes = useStyles();
  const open = Boolean(anchorEl);

  useEffect(() => {
    if (user) {
      const {
        attributes: { name },
      } = user;
      setUserName(name);
    }
  }, [user]);

  const onOpenMenu = event => {
    setAnchorEl(event.currentTarget);
  };

  const onCloseMenu = () => {
    setAnchorEl(null);
  };

  const onLogOut = () => {
    setUserName('');
    onCloseMenu();
    logOut();
  };

  return (
    <AppBar position="relative">
      <Toolbar>
        <IconButton edge="start" className={classes.menuButton} color="inherit" aria-label="menu">
          <MenuIcon />
        </IconButton>
        <Link className={classes.title} to="/workspace">
          <Typography align="center" variant="h6">
            Grapher
            <BubbleChart style={{ marginLeft: '5px' }} />
          </Typography>
        </Link>

        {userName ? (
          <>
            <Typography className={classes.paragraph} variant="body2">
              You are logged in as <strong>{userName}</strong>
            </Typography>
            <div>
              <IconButton
                aria-label="account of current user"
                aria-controls="menu-appbar"
                aria-haspopup="true"
                onClick={onOpenMenu}
                color="inherit"
              >
                <AccountCircle />
              </IconButton>
              <Menu
                id="menu-appbar"
                anchorEl={anchorEl}
                anchorOrigin={{
                  vertical: 'top',
                  horizontal: 'right',
                }}
                keepMounted
                transformOrigin={{
                  vertical: 'top',
                  horizontal: 'right',
                }}
                open={open}
                onClose={onCloseMenu}
              >
                <MenuItem onClick={onCloseMenu}>
                  <Link className={classes.link} to="/change-password">
                    Change password
                  </Link>
                </MenuItem>
                <MenuItem onClick={onLogOut}>Logout</MenuItem>
              </Menu>
            </div>
          </>
        ) : (
          <Link className={classes.button} to="/signin">
            <Button color="inherit">Login</Button>
          </Link>
        )}
      </Toolbar>
    </AppBar>
  );
};

export default NavBar;
