import React, { useState, useContext, createContext, useEffect } from 'react';
import Amplify, { Auth } from 'aws-amplify';
import { useHistory } from 'react-router-dom';
import PropTypes from 'prop-types';

import config from '../static/config.json';

const authContext = createContext();

export const useAuth = () => {
  return useContext(authContext);
};

const useProvideAuth = () => {
  const [loggedUser, setLoggedUser] = useState(null);
  const [sessionData, setSessionData] = useState(null);
  const history = useHistory();

  useEffect(() => {
    const getSession = async () => {
      try {
        const session = await Auth.currentSession();
        const user = await Auth.currentAuthenticatedUser();
        setSessionData(session);
        setLoggedUser(user);
      } catch (err) {
        console.log(err);
      }
    };
    getSession();
  }, []);

  const signIn = async (username, password) => {
    let user;
    let error;
    try {
      user = await Auth.signIn(username, password);
      setLoggedUser(user);
    } catch (err) {
      error = err;
    }
    return new Promise((resolve, reject) => {
      if (error) {
        reject(error);
      }
      resolve(user);
    });
  };

  const signUp = async (email, password, userName) =>
    Auth.signUp({ username: email, password, attributes: { name: userName } });

  const getSession = () => Auth.currentSession();

  const logOut = async () => {
    try {
      setLoggedUser(null);
      Auth.signOut().then(() => history.push('signin'));
    } catch (err) {
      console.error(err);
    }
  };

  const changePassword = async (password, newPassword) => {
    const user = await Auth.currentAuthenticatedUser();
    return Auth.changePassword(user, password, newPassword);
  };

  const resetPassword = async email => {
    try {
      await Auth.forgotPassword(email);
      history.push('new-password');
    } catch (err) {
      console.log(err);
    }
  };

  const submitNewPassword = (email, code, password) => {
    return Auth.forgotPasswordSubmit(email, code, password);
  };

  const isAuthenticated = () => !!loggedUser;

  return {
    user: loggedUser,
    session: sessionData,
    signIn,
    signUp,
    getSession,
    logOut,
    isAuthenticated,
    changePassword,
    resetPassword,
    submitNewPassword,
  };
};

export const AuthProvider = ({ children }) => {
  Amplify.configure({
    Auth: {
      mandatorySignIn: true,
      region: config.cognito.REGION,
      userPoolId: config.cognito.USER_POOL_ID,
      userPoolWebClientId: config.cognito.APP_CLIENT_ID,
    },
  });
  const auth = useProvideAuth();

  return <authContext.Provider value={auth}>{children}</authContext.Provider>;
};

AuthProvider.defaultProps = {
  children: [],
};

AuthProvider.propTypes = {
  children: PropTypes.oneOfType([PropTypes.arrayOf(PropTypes.node), PropTypes.node]),
};
