import { useEffect, useReducer, useCallback } from 'react';

const blacklistedTargets = ['INPUT', 'TEXTAREA'];

const keysReducer = (state, action) => {
  switch (action.type) {
    case 'set-key-down':
      return { ...state, [action.key]: true };
    case 'set-key-up':
      return { ...state, [action.key]: false };
    default:
      return state;
  }
};

const useKeyboardShortcut = (shortcutKeys, callback) => {
  if (!Array.isArray(shortcutKeys) || !shortcutKeys.length) {
    throw new Error(
      'The first parameter to `useKeyboardShortcut` must be an array containing at least one `KeyboardEvent.key` string.'
    );
  }
  if (!callback || typeof callback !== 'function') {
    throw new Error(
      'The second parameter to `useKeyboardShortcut` must be a function that will be invoked when the keys are pressed.'
    );
  }

  const initialKeyMapping = shortcutKeys.reduce(
    (currentKeys, key) => ({
      ...currentKeys,
      [key]: false,
    }),
    {}
  );

  const [keys, setKeys] = useReducer(keysReducer, initialKeyMapping);

  const keydownListener = useCallback(
    keydownEvent => {
      const { key, target, repeat } = keydownEvent;
      if (repeat) return;
      if (blacklistedTargets.includes(target.tagName)) return;
      if (!shortcutKeys.includes(key)) return;

      if (!keys[key]) {
        setKeys({ type: 'set-key-down', key });
      }
    },
    [shortcutKeys, keys]
  );

  const keyupListener = useCallback(
    keyupEvent => {
      const { key, target } = keyupEvent;
      if (blacklistedTargets.includes(target.tagName)) return;
      if (!shortcutKeys.includes(key)) return;

      if (keys[key]) {
        setKeys({ type: 'set-key-up', key });
      }
    },
    [shortcutKeys, keys]
  );

  useEffect(() => {
    window.addEventListener('keydown', keydownListener, true);

    return () => window.removeEventListener('keydown', keydownListener, true);
  }, [keydownListener]);

  useEffect(() => {
    window.addEventListener('keyup', keyupListener, true);

    return () => window.removeEventListener('keyup', keyupListener, true);
  }, [keyupListener]);

  useEffect(() => {
    if (!Object.values(keys).filter(value => !value).length) callback(keys);
  }, [callback, keys]);
};

export default useKeyboardShortcut;
