import React, { useState } from 'react';
import { Link, Redirect } from 'react-router-dom';

import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import InputAdornment from '@material-ui/core/InputAdornment';
import LockIcon from '@material-ui/icons/Lock';
import MailIcon from '@material-ui/icons/Mail';
import TextField from '@material-ui/core/TextField';
import Typography from '@material-ui/core/Typography';

import { useAuth } from '../../hooks/useAuth';

const useStyles = makeStyles({
  wrapper: {
    flexGrow: 1,
    width: '60%',
    margin: '0 auto',
    display: 'flex',
    justifyContent: 'center',
    flexDirection: 'column',
    padding: '3rem',
  },
  header: {
    fontWeight: 700,
    letterSpacing: '1.5px',
  },
  errorWrapper: {
    minHeight: '1.5rem',
  },
  hidden: {
    visibility: 'hidden',
  },
  form: {
    display: 'flex',
    flexDirection: 'column',
    maxWidth: '25rem',
    marginTop: '1rem',
    marginBottom: '1rem',
  },
  input: {
    marginBottom: '1.5rem',
  },
  buttonWrapper: {
    display: 'flex',
    justifyContent: 'flex-end',
  },
  link: {
    color: 'inherit',
  },
});

const SignIn = () => {
  const { signIn, isAuthenticated } = useAuth();
  const classes = useStyles();
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');

  const handleChangeUserProperty = fn => event => {
    const {
      target: { value },
    } = event;
    fn(value);
  };

  const handleSubmit = e => {
    e.preventDefault();
    signIn(email, password).catch(err => setError(err.message));
  };

  if (isAuthenticated()) {
    return <Redirect to="workspace" />;
  }

  return (
    <div className={classes.wrapper}>
      <Typography className={classes.header} variant="h4" component="h1" color="textPrimary">
        Sign up
      </Typography>
      <div className={classes.errorWrapper}>
        <Typography
          className={!error && classes.hidden}
          variant="body1"
          component="p"
          color="error"
        >
          {error}
        </Typography>
      </div>
      <form className={classes.form} onSubmit={handleSubmit}>
        <TextField
          className={classes.input}
          label="E-MAIL"
          type="email"
          value={email}
          onChange={handleChangeUserProperty(setEmail)}
          InputProps={{
            startAdornment: (
              <InputAdornment position="start">
                <MailIcon />
              </InputAdornment>
            ),
          }}
          required
        />
        <TextField
          className={classes.input}
          label="PASSWORD"
          type="password"
          value={password}
          onChange={handleChangeUserProperty(setPassword)}
          InputProps={{
            startAdornment: (
              <InputAdornment position="start">
                <LockIcon />
              </InputAdornment>
            ),
          }}
          required
        />
        <Typography variant="body2" component="p">
          <Link to="/forgot-password">Forgot password?</Link>
        </Typography>
        <div className={classes.buttonWrapper}>
          <Button type="submit" variant="contained" color="primary">
            Sign In
          </Button>
        </div>
      </form>
      <Typography variant="subtitle1" component="p">
        Don&#39;t you have account? You can <Link to="/signup">Sign Up</Link>.
      </Typography>
    </div>
  );
};

export default SignIn;
