import React, { useState } from 'react';
import { Redirect } from 'react-router-dom';

import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import InputAdornment from '@material-ui/core/InputAdornment';
import MailIcon from '@material-ui/icons/Mail';
import TextField from '@material-ui/core/TextField';
import Typography from '@material-ui/core/Typography';

import { useAuth } from '../../hooks/useAuth';

const useStyles = makeStyles({
  wrapper: {
    flexGrow: 1,
    width: '60%',
    margin: '0 auto',
    display: 'flex',
    justifyContent: 'center',
    flexDirection: 'column',
    padding: '3rem',
  },
  header: {
    fontWeight: 700,
    letterSpacing: '1.5px',
    marginBottom: '.5rem',
  },
  infoText: {
    marginBottom: '1.5rem',
  },
  errorWrapper: {
    minHeight: '1.5rem',
  },
  successMessage: {
    color: '#4caf50',
  },
  hidden: {
    visibility: 'hidden',
  },
  form: {
    display: 'flex',
    flexDirection: 'column',
    maxWidth: '25rem',
    marginTop: '1rem',
  },
  input: {
    marginBottom: '1.5rem',
  },
  buttonWrapper: {
    display: 'flex',
    justifyContent: 'flex-end',
  },
});

const ForgotPassword = () => {
  const [email, setEmail] = useState('');
  const { resetPassword, isAuthenticated } = useAuth();

  const classes = useStyles();

  const handleChangeUserProperty = fn => event => {
    const {
      target: { value },
    } = event;
    fn(value);
  };

  const handleSubmit = e => {
    e.preventDefault();
    if (email) {
      resetPassword(email);
    }
  };

  if (isAuthenticated()) {
    return <Redirect to="workspace" />;
  }

  return (
    <div className={classes.wrapper}>
      <Typography className={classes.header} variant="h4" component="h1" color="textPrimary">
        Forgot your password?
      </Typography>
      <Typography className={classes.infoText} variant="body1" component="p">
        Please enter the email address associated with your account and we will email you a password
        reset code.
      </Typography>
      <form className={classes.form} onSubmit={handleSubmit}>
        <TextField
          className={classes.input}
          label="E-MAIL"
          type="email"
          value={email}
          onChange={handleChangeUserProperty(setEmail)}
          InputProps={{
            startAdornment: (
              <InputAdornment position="start">
                <MailIcon />
              </InputAdornment>
            ),
          }}
          required
        />
        <div className={classes.buttonWrapper}>
          <Button type="submit" variant="contained" color="primary">
            Send
          </Button>
        </div>
      </form>
    </div>
  );
};

export default ForgotPassword;
