import React from 'react';
import { Switch, Route } from 'react-router-dom';

import ChangePassword from 'routes/ChangePassword';
import ConfirmEmailPage from 'routes/ConfirmEmailPage';
import NavBar from 'components/NavBar';
import Register from 'routes/Register';
import SecuredRoute from 'components/SecuredRoute';
import SignIn from 'routes/SignIn';
import Workspace from 'routes/Workspace';
import ForgotPassword from 'routes/ForgotPassword';
import ForgotPasswordConfirmationPage from 'routes/ForgotPasswordConfirmationPage';
import './App.scss';

function App() {
  return (
    <div className="App">
      <NavBar />
      <Switch>
        <Route exact path="/signup" component={Register} />
        <Route exact path="/signin" component={SignIn} />
        <Route exact path="/confirm-email" component={ConfirmEmailPage} />
        <Route exact path="/forgot-password" component={ForgotPassword} />
        <Route exact path="/new-password" component={ForgotPasswordConfirmationPage} />
        <SecuredRoute exact path="/change-password">
          <ChangePassword />
        </SecuredRoute>
        <SecuredRoute path="/workspace">
          <Workspace />
        </SecuredRoute>
        <SecuredRoute path="/">
          <Workspace />
        </SecuredRoute>
      </Switch>
    </div>
  );
}

export default App;
