import React, { useState } from 'react';
import { useHistory, Redirect } from 'react-router-dom';

import { makeStyles } from '@material-ui/core/styles';
import AccountCircle from '@material-ui/icons/AccountCircle';
import Button from '@material-ui/core/Button';
import InputAdornment from '@material-ui/core/InputAdornment';
import LockIcon from '@material-ui/icons/Lock';
import MailIcon from '@material-ui/icons/Mail';
import TextField from '@material-ui/core/TextField';
import Typography from '@material-ui/core/Typography';

import { useAuth } from '../../hooks/useAuth';

const useStyles = makeStyles({
  wrapper: {
    flexGrow: 1,
    width: '60%',
    margin: '0 auto',
    display: 'flex',
    justifyContent: 'center',
    flexDirection: 'column',
    padding: '3rem',
  },
  header: {
    fontWeight: 700,
    letterSpacing: '1.5px',
  },
  errorWrapper: {
    minHeight: '1.5rem',
  },
  hidden: {
    visibility: 'hidden',
  },
  form: {
    display: 'flex',
    flexDirection: 'column',
    maxWidth: '25rem',
    marginTop: '1rem',
  },
  input: {
    marginBottom: '1.5rem',
  },
  buttonWrapper: {
    display: 'flex',
    justifyContent: 'flex-end',
  },
});

const handleChangeUserProperty = fn => event => {
  const {
    target: { value },
  } = event;
  fn(value);
};

const Register = () => {
  const classes = useStyles();
  const { signUp, isAuthenticated } = useAuth();
  const history = useHistory();

  const [userName, setUserName] = useState('');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [error, setError] = useState('');

  const handleSubmit = e => {
    e.preventDefault();
    if (error) setError('');
    if (password === confirmPassword) {
      signUp(email, password, userName)
        .then(() => history.push('confirm-email'))
        .catch(err => setError(err.message));
    } else {
      setError('Passwords do not match');
    }
  };

  if (isAuthenticated()) {
    return <Redirect to="workspace" />;
  }

  return (
    <div className={classes.wrapper}>
      <Typography className={classes.header} variant="h4" component="h1" color="textPrimary">
        Sign up
      </Typography>
      <div className={classes.errorWrapper}>
        <Typography variant="body1" component="p" color="error">
          {error}
        </Typography>
      </div>
      <form className={classes.form} onSubmit={handleSubmit}>
        <TextField
          className={classes.input}
          label="USER NAME"
          value={userName}
          onChange={handleChangeUserProperty(setUserName)}
          InputProps={{
            startAdornment: (
              <InputAdornment position="start">
                <AccountCircle />
              </InputAdornment>
            ),
          }}
          required
        />
        <TextField
          className={classes.input}
          label="E-MAIL"
          type="email"
          value={email}
          onChange={handleChangeUserProperty(setEmail)}
          InputProps={{
            startAdornment: (
              <InputAdornment position="start">
                <MailIcon />
              </InputAdornment>
            ),
          }}
          required
        />
        <TextField
          className={classes.input}
          label="PASSWORD"
          type="password"
          value={password}
          onChange={handleChangeUserProperty(setPassword)}
          InputProps={{
            startAdornment: (
              <InputAdornment position="start">
                <LockIcon />
              </InputAdornment>
            ),
          }}
          required
        />
        <TextField
          className={classes.input}
          label="CONFIRM PASSWORD"
          type="password"
          value={confirmPassword}
          onChange={handleChangeUserProperty(setConfirmPassword)}
          InputProps={{
            startAdornment: (
              <InputAdornment position="start">
                <LockIcon />
              </InputAdornment>
            ),
          }}
          required
        />
        <div className={classes.buttonWrapper}>
          <Button type="submit" variant="contained" color="primary">
            Sign Up
          </Button>
        </div>
      </form>
    </div>
  );
};

export default Register;
