import React, { useState, useEffect, useCallback } from 'react';
import { shallowEqual, useSelector } from 'react-redux';
import { v4 as uuid } from 'uuid';

import Button from '@material-ui/core/Button';
import Container from '@material-ui/core/Container';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import Grid from '@material-ui/core/Grid';
import InputLabel from '@material-ui/core/InputLabel';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import TextField from '@material-ui/core/TextField';
import { Typography } from '@material-ui/core';
import Select from '@material-ui/core/Select';

import { useDispatchedActions, useKeyboardShortcut } from 'hooks';
import {
  getGraphData as getGraphDataAction,
  createNode as createNodeAction,
  updateNode as updateNodeAction,
  deleteNode as deleteNodeAction,
  addLink as addLinkAction,
  deleteLink as deleteLinkAction,
} from './actions';
import { TEMPORARY_SPACE_VALUE } from './constants';

import Graph from './components/Graph';
import Orphanage from './components/Orphanage';

import './Workspace.scss';

const graphConfig = {
  automaticRearrangeAfterDropNode: false,
  collapsible: true,
  directed: true,
  nodeHighlightBehavior: true,
  linkHighlightBehavior: true,
  minZoom: 1,
  d3: {
    gravity: -500
  },
  node: {
    labelProperty: 'name',
    color: '#d3d3d3',
    fontColor: 'black',
    fontSize: 8,
    fontWeight: 'normal',
    highlightColor: '#3f51b5',
    highlightFontSize: 8,
    highlightFontWeight: 'normal',
    highlightStrokeColor: 'SAME',
    highlightStrokeWidth: 'SAME',
    mouseCursor: 'pointer',
    opacity: 1,
    size: 200,
    strokeColor: 'none',
    strokeWidth: 1.5,
    symbolType: 'circle',
  },
  link: {
    highlightColor: 'black'
  }
};

const contextMenuInitialState = { mouseX: null, mouseY: null };

const Workspace = () => {
  const {
    getGraphData,
    createNode,
    updateNode,
    deleteNode,
    addLink,
    deleteLink,
  } = useDispatchedActions({
    getGraphData: getGraphDataAction,
    createNode: createNodeAction,
    updateNode: updateNodeAction,
    deleteNode: deleteNodeAction,
    addLink: addLinkAction,
    deleteLink: deleteLinkAction,
  });
  const { selectedGraph, orphans, nodesByIds, fetching } = useSelector(
    ({ workspaceReducer }) => ({
      selectedGraph: workspaceReducer.selectedGraph,
      orphans: workspaceReducer.orphans,
      nodesByIds: workspaceReducer.nodesByIds,
      fetching: workspaceReducer.fetching,
    }),
    shallowEqual
  );
  const [isDialogOpen, setDialogOpen] = useState(false);
  const [isEditDialogOpen, setEditDialogOpen] = useState(false);
  const [isDeleteDialogOpen, setDeleteDialogOpen] = useState(false);
  const [isLinkDialogOpen, setLinkDialogOpen] = useState(false);
  const [targetNodeId, setTargetNodeId] = useState('');
  const [newNodeName, setNewNodeName] = useState('');
  const [nodeToDeleteName, setNodeToDeleteName] = useState('');
  const [contextMenu, setContextMenu] = useState(contextMenuInitialState);
  const [linkContextMenu, setLinkContextMenu] = useState(contextMenuInitialState);
  const [focusedNodeId, setFocusedNodeId] = useState(null);
  const [focusedLinkId, setFocusedLinkId] = useState(null);
  const { nodes: graphNodes = [], links = [] } = selectedGraph || {};

  const fetchGraphData = useCallback(getGraphData, []);

  useEffect(() => {
    fetchGraphData();
  }, [fetchGraphData]);

  const handleOpenDialog = () => setDialogOpen(true);

  const handleOpenEditDialog = () => {
    const editedNodeName = nodesByIds[focusedNodeId].name;
    setContextMenu(contextMenuInitialState);
    setNewNodeName(editedNodeName);
    setEditDialogOpen(true);
  };

  const handleOpenDeleteDialog = () => {
    const { nodes } = selectedGraph;
    const { name } = [...nodes, ...orphans].find(({ id }) => id === focusedNodeId);
    setNodeToDeleteName(name);
    setContextMenu(contextMenuInitialState);
    setDeleteDialogOpen(true);
  };

  const handleCloseDialog = () => {
    setDialogOpen(false);
    setNewNodeName('');
  };

  const handleCloseEditDialog = () => {
    setEditDialogOpen(false);
    setNewNodeName('');
    setFocusedNodeId(null);
  };

  const handleCloseDeleteDialog = () => {
    setDeleteDialogOpen(false);
    setNodeToDeleteName('');
    setFocusedNodeId(null);
  };

  const handleOpenAddLinkDialog = () => {
    setContextMenu(contextMenuInitialState);
    setLinkDialogOpen(true);
  };

  const handleOpenDeleteLinkDialog = () => {
    setLinkContextMenu(contextMenuInitialState);
    handleOpenAddLinkDialog();
  };

  const handleCloseLinkDialog = () => {
    setLinkDialogOpen(false);
    setFocusedNodeId('');
    setFocusedLinkId('');
    setTargetNodeId('');
  };

  useKeyboardShortcut(['Shift', 'N'], () => {
    if (!isDialogOpen) handleOpenDialog();
  });

  const onChangeNodeName = e => {
    e.preventDefault();
    const {
      target: { value },
    } = e;
    setNewNodeName(value);
  };

  const handleAddNode = () => {
    // const id = uuid();
    const newNode = {
      // id,
      properties: {
        name: newNodeName,
        space: TEMPORARY_SPACE_VALUE,
      },
    };
    createNode(newNode).then(() => {
      fetchGraphData();
      setNewNodeName('');
      handleCloseDialog();
    });
  };

  const handleChangeTargetNode = e => {
    const {
      target: { value },
    } = e;
    setTargetNodeId(value);
  };

  const handleAddLink = () => {
    const id = uuid();
    const newLink = {
      id,
      source: focusedNodeId,
      target: targetNodeId,
      properties: {
        space: TEMPORARY_SPACE_VALUE,
        type: 'API',
      },
    };
    addLink(newLink).then(() => {
      fetchGraphData();
      handleCloseLinkDialog();
    });
  };

  const handleDeleteLink = () => {
    deleteLink(focusedLinkId).then(() => {
      fetchGraphData();
      handleCloseLinkDialog();
    });
  };

  const handleUpdateNode = () => {
    const { nodes } = selectedGraph;
    const { id, properties } = [...nodes, ...orphans].find(
      ({ id: nodeId }) => nodeId === focusedNodeId
    );
    const updatedNode = {
      id,
      properties: {
        ...properties,
        name: newNodeName,
      },
    };
    updateNode(updatedNode).then(() => {
      fetchGraphData();
      handleCloseEditDialog();
    });
  };

  const handleDeleteNode = () => {
    deleteNode(focusedNodeId).then(() => {
      fetchGraphData();
      handleCloseDeleteDialog();
    });
  };

  const onRightClickNode = (event, id) => {
    event.preventDefault();
    setFocusedNodeId(id);
    setContextMenu({ mouseX: event.clientX, mouseY: event.clientY });
  };

  const onRightClickLink = (event, sourceId, targetId) => {
    event.preventDefault();
    const { id, target, source } = links.find(
      link => link.source === sourceId && link.target === targetId
    );
    setFocusedLinkId(id);
    setFocusedNodeId(source);
    setTargetNodeId(target);
    setLinkContextMenu({ mouseX: event.clientX, mouseY: event.clientY });
  };

  const handleCloseContextMenu = () => {
    setContextMenu(contextMenuInitialState);
    setFocusedNodeId(null);
  };

  const handleCloseLinkContextMenu = () => {
    setLinkContextMenu(contextMenuInitialState);
    setFocusedLinkId(null);
  };

  const getTargetNodes = (nodes, selectedNodeId) => {
    return nodes.filter(node => node.id !== selectedNodeId);
  };

  const graphWrapper = document.getElementById('Workspace__Graph-wrapper');

  return (
    <Grid container className="Workspace" spacing={0}>
      <Grid item xs={9}>
        <Container id="Workspace__Graph-wrapper" className="Workspace__Graph-wrapper" fixed>
          {selectedGraph && graphWrapper && (
            <Graph
              selectedGraph={selectedGraph}
              graphConfig={{
                ...graphConfig,
                width: graphWrapper.offsetWidth,
                height: graphWrapper.offsetHeight,
              }}
              onRightClickNode={onRightClickNode}
              onRightClickLink={onRightClickLink}
            />
          )}
        </Container>
        <Dialog
          open={isLinkDialogOpen}
          onClose={handleCloseLinkDialog}
          aria-labelledby="form-dialog-title"
        >
          <DialogTitle id="form-dialog-title">
            {!focusedLinkId ? 'Create new link' : 'Delete link'}
          </DialogTitle>
          <DialogContent>
            <InputLabel id="Workspace__source-node-select">Source node: </InputLabel>
            <Select
              labelId="Workspace__source-node-select"
              className="Workspace__add-link-select"
              value={focusedNodeId}
              disabled
            >
              {[...graphNodes, ...(orphans || [])].map(({ id, name }) => (
                <MenuItem key={id} value={id}>
                  {name}
                </MenuItem>
              ))}
            </Select>
            <InputLabel id="Workspace__target-node-select">Target node: </InputLabel>
            <Select
              className="Workspace__add-link-select"
              labelId="Workspace__target-node-select"
              value={targetNodeId}
              onChange={handleChangeTargetNode}
              disabled={!!focusedLinkId}
            >
              {getTargetNodes(graphNodes, focusedNodeId).map(({ id, name }) => (
                <MenuItem key={id} value={id}>
                  {name}
                </MenuItem>
              ))}
            </Select>
          </DialogContent>
          <DialogActions>
            {!focusedLinkId ? (
              <Button
                onClick={handleAddLink}
                color="primary"
                disabled={!targetNodeId || fetching.addLink}
              >
                Add link
              </Button>
            ) : (
              <Button onClick={handleDeleteLink} color="primary" disabled={fetching.deleteLink}>
                Confirm
              </Button>
            )}
            <Button onClick={handleCloseLinkDialog}>Cancel</Button>
          </DialogActions>
        </Dialog>
        <Dialog open={isDialogOpen} onClose={handleCloseDialog} aria-labelledby="form-dialog-title">
          <DialogTitle id="form-dialog-title">Create new node</DialogTitle>
          <DialogContent>
            <TextField
              margin="dense"
              id="name"
              label="Node name"
              fullWidth
              onChange={onChangeNodeName}
            />
          </DialogContent>
          <DialogActions>
            <Button
              onClick={handleAddNode}
              color="primary"
              disabled={!newNodeName || fetching.createNode}
            >
              Add node
            </Button>
            <Button onClick={handleCloseDialog}>Cancel</Button>
          </DialogActions>
        </Dialog>
        <Dialog
          open={isEditDialogOpen}
          onClose={handleCloseDialog}
          aria-labelledby="form-dialog-title"
        >
          <DialogTitle id="form-dialog-title">Edit node</DialogTitle>
          <DialogContent>
            <TextField
              margin="dense"
              id="name"
              label="Node name"
              value={newNodeName}
              fullWidth
              onChange={onChangeNodeName}
            />
          </DialogContent>
          <DialogActions>
            <Button
              onClick={handleUpdateNode}
              color="primary"
              disabled={!newNodeName || fetching.updateNode}
            >
              Update
            </Button>
            <Button onClick={handleCloseEditDialog}>Cancel</Button>
          </DialogActions>
        </Dialog>
        <Dialog
          open={isDeleteDialogOpen}
          onClose={handleCloseDeleteDialog}
          aria-labelledby="form-dialog-title"
        >
          <DialogTitle id="form-dialog-title">Delete node</DialogTitle>
          <DialogContent>
            <Typography variant="body1">
              Do you want to delete node <strong>{nodeToDeleteName}</strong>?
            </Typography>
          </DialogContent>
          <DialogActions>
            <Button onClick={handleDeleteNode} color="primary">
              Delete
            </Button>
            <Button onClick={handleCloseDeleteDialog}>Cancel</Button>
          </DialogActions>
        </Dialog>
      </Grid>
      <Grid className="Workspace__Graph-wrapper" item xs={3}>
        <Orphanage
          orphans={orphans}
          handleOpenDialog={handleOpenDialog}
          onRightClickNode={onRightClickNode}
        />
      </Grid>
      <Menu
        keepMounted
        open={contextMenu.mouseY !== null}
        onClose={handleCloseContextMenu}
        anchorReference="anchorPosition"
        anchorPosition={
          contextMenu.mouseY !== null && contextMenu.mouseX !== null
            ? { top: contextMenu.mouseY, left: contextMenu.mouseX }
            : undefined
        }
      >
        <MenuItem onClick={handleOpenAddLinkDialog}>Add Link</MenuItem>
        <MenuItem onClick={handleOpenEditDialog}>Edit node</MenuItem>
        <MenuItem onClick={handleOpenDeleteDialog}>Delete node</MenuItem>
      </Menu>
      <Menu
        keepMounted
        open={linkContextMenu.mouseY !== null}
        onClose={handleCloseLinkContextMenu}
        anchorReference="anchorPosition"
        anchorPosition={
          linkContextMenu.mouseY !== null && linkContextMenu.mouseX !== null
            ? { top: linkContextMenu.mouseY, left: linkContextMenu.mouseX }
            : undefined
        }
      >
        <MenuItem onClick={handleOpenDeleteLinkDialog}>Delete Link</MenuItem>
      </Menu>
    </Grid>
  );
};

export default Workspace;
