import {
  getApiElements,
  postApiElements,
  putApiElement,
  deleteApiElement,
} from 'services/api/utils';
import {
  FETCH_GRAPH_DATA,
  CREATE_NODE,
  UPDATE_NODE,
  DELETE_NODE,
  ADD_LINK,
  DELETE_LINK,
} from './constants';

const GET_GRAPH_URL = 'https://grapher-app.herokuapp.com/graphs?space=20';
const ADD_NODE_URL = 'https://grapher-app.herokuapp.com/graphs/nodes';
const ADD_LINK_URL = 'https://grapher-app.herokuapp.com/graphs/links';
export const getGraphData = () => getApiElements(GET_GRAPH_URL, FETCH_GRAPH_DATA);

export const createNode = newNode => postApiElements(ADD_NODE_URL, CREATE_NODE, newNode);

export const updateNode = updatedNode =>
  putApiElement(`${ADD_NODE_URL}/${updatedNode.id}`, UPDATE_NODE, updatedNode);

export const deleteNode = id => deleteApiElement(`${ADD_NODE_URL}/${id}`, DELETE_NODE);

export const addLink = newLink => postApiElements(ADD_LINK_URL, ADD_LINK, newLink);

export const deleteLink = id => deleteApiElement(`${ADD_LINK_URL}/${id}`, DELETE_LINK);
